@import 'https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css';
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif; }

body * {
  box-sizing: inherit; }

svg.icon {
  fill: currentColor;
  width: 1em;
  height: 1em; }

#content {
  background-color: #fafafa;
  min-height: 65vh; }

.content-wrapper a.read-more,
.content-wrapper a:not([class]) {
  padding-left: 0.1em;
  padding-right: 0.1em;
  color: #0a0a0a;
  text-decoration: underline #D72027; }

.content-wrapper a.read-more:hover,
.content-wrapper a:not([class]):hover {
  text-decoration: none; }

*:focus {
  outline: 1px dashed #0a0a0a; }

.no-bullet {
  padding-left: 0;
  list-style-type: none; }

.inline-list > li {
  display: inline-block; }

.article-content {
  padding: 0 1rem; }

@media (min-width: 42em) {
  .article-content {
    padding: 0; } }

.clearfix::before,
.clearfix::after {
  display: table;
  content: '';
  clear: both; }

.section-margin {
  margin-top: 1rem;
  margin-bottom: 1rem; }

@media (max-width: 42em) {
  .hide-small {
    display: none !important; } }

@media (min-width: 42em) and (max-width: 60em) {
  .hide-medium {
    display: none !important; } }

@media (min-width: 42em) {
  .hide-medium-up {
    display: none !important; } }

@media (max-width: 42em) {
  .hide-medium-down {
    display: none !important; } }

@media (min-width: 42em) and (max-width: 60em) {
  .hide-large {
    display: none !important; } }

@media (min-width: 60em) {
  .hide-large-up {
    display: none !important; } }

@media (max-width: 60em) {
  .hide-large-down {
    display: none !important; } }

.footer {
  margin-top: 2rem;
  padding: 1rem;
  background-color: #0a0a0a;
  color: #fafafa; }

.footer-icon {
  fill: currentColor;
  padding: 0.5rem;
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%; }

.footer-icon:hover {
  background-color: #3b5998; }

.footer a {
  display: inline-block;
  color: #fafafa;
  text-decoration: underline; }

.footer a:hover {
  text-decoration: none; }

.footer-contact {
  flex-direction: column;
  justify-items: center;
  align-items: flex-start;
  text-align: center; }

@media (min-width: 60em) {
  .footer-contact {
    text-align: left; } }

.footer-social {
  align-items: center; }

.footer-copy {
  padding: 1rem 0 0; }

@media (max-width: 42em) {
  .footer-social.column.text-right,
  .footer-contact {
    align-items: center;
    justify-content: center;
    text-align: center; }
  .footer-social {
    margin-top: 0.5rem; } }

.row {
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: 1024px; }

@media (min-width: 42em) {
  .grid {
    display: flex; } }

.grid > .column {
  display: flex;
  flex-wrap: wrap;
  flex: 1; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.column.text-right {
  justify-content: flex-end; }

.text-center {
  text-align: center; }

.float-right {
  float: right; }

@media (max-width: 42em) {
  .reverse-order-small {
    display: flex;
    flex-direction: column-reverse;
    flex-wrap: wrap; } }

h1, h2, h3, h4, h5, h6 {
  font-family: Montserrat, Verdana, Geneva, sans-serif; }

.show-for-sr {
  position: absolute !important;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0; }

/* Skip links */
.skip-link {
  position: absolute;
  top: -2rem;
  left: 2%;
  color: #fafafa;
  z-index: 2; }

.admin-bar .skip-link:focus {
  top: 2rem; }

.skip-link:focus {
  top: 0;
  padding: 1rem;
  background-color: #0a0a0a; }

/* Prenav */
.prenav {
  border-top: 5px solid #D72027;
  background-color: #0a0a0a; }

.prenav .grid {
  display: flex; }

.header-logo {
  display: flex;
  position: relative;
  z-index: 1;
  flex: 1 1 auto;
  align-items: center;
  color: #fafafa;
  text-decoration: none;
  z-index: 1; }

.header-logo:hover .header-logo__text {
  text-decoration: underline; }

.header-logo img {
  margin: 0.25rem 1rem;
  height: 3rem; }

.header-logo__text {
  padding-right: 3.5rem;
  font-size: 1.25rem;
  font-weight: bold; }

@media (min-width: 60em) {
  .header-logo img {
    margin: 0.25rem 1rem -0.4em 0;
    height: 5rem; }
  .header-logo__text {
    font-size: 2rem; } }

.prenav .prenav-menu {
  display: flex;
  justify-content: flex-end;
  height: 100%; }

.prenav-menu li {
  display: inline-flex;
  height: 100%; }

.button-red,
.prenav-menu li a {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #D72027;
  color: #fafafa; }

.prenav-menu li a {
  border: 1px solid #0a0a0a;
  border-right: 0 solid transparent; }

.button-red:hover,
.prenav-menu li a:hover {
  background-color: #333333; }

.button-red {
  padding: 1rem 2rem;
  width: auto;
  text-decoration: none; }

.button-red:hover {
  text-decoration: underline; }

.prenav-menu li a:hover {
  border-bottom: 1px solid #0a0a0a; }

/* Navbar container  */
.menu-button {
  position: fixed;
  top: 0;
  right: 0;
  display: block;
  z-index: 5;
  width: 2.5rem;
  height: 3rem;
  border: none;
  background: rgba(10, 10, 10, 0.9);
  color: #fafafa;
  border-top: 5px solid #D72027;
  transition: right 0.3s; }

.menu-button::before,
.menu-button::after {
  content: '';
  display: block;
  margin: 0 auto;
  width: 1.5em;
  border-top: 3px solid white;
  transition: all 0.2s; }

.menu-button::after {
  height: 3px;
  border-bottom: 3px solid white; }

.menu-button::before {
  margin-bottom: 3px; }

.menu-button.is-open {
  right: 250px; }
  .menu-button.is-open::before, .menu-button.is-open::after {
    position: absolute;
    top: 50%;
    left: 50%; }
  .menu-button.is-open::before {
    transform: translate(-50%, -50%) rotate(45deg); }
  .menu-button.is-open::after {
    transform: translate(-50%, -50%) rotate(-45deg);
    border-bottom: none; }

.nav-main {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
  width: 100%;
  max-width: 250px;
  max-height: 90%;
  background-color: #0a0a0a;
  border-top: 5px solid #D72027;
  transform: translatex(100%);
  transition: transform 0.2s ease-out;
  overflow-y: auto; }

.nav-main.is-open {
  transform: translatex(0); }

@media (min-width: 60em) {
  .nav-main {
    position: static;
    transform: none;
    width: 100%;
    max-width: none;
    border-top: none;
    background-color: rgba(10, 10, 10, 0.8);
    transition: transform 0s;
    overflow-y: hidden; } }

.nav-main .navbar {
  display: flex;
  flex-direction: column; }

.navbar {
  overflow: hidden;
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 0; }

@media (min-width: 60em) {
  .navbar {
    padding: 0 0 0 0; }
  .nav-main .navbar,
  .navbar {
    display: table; } }

.navbar > li {
  display: table-cell; }

.navbar ul {
  list-style-type: none;
  padding-left: 0; }

/* Links inside the navbar */
.navbar a {
  display: inline-block;
  font-size: 16px;
  color: #fafafa;
  text-align: center;
  padding: 1rem 2rem 1rem 0.5rem;
  text-align: left;
  text-decoration: none; }

.prenav a:focus,
.navbar a:focus {
  outline: 1px dashed #D72027;
  outline-offset: -1px; }

@media (min-width: 42em) {
  .navbar a {
    padding: 14px 16px;
    text-align: center; } }

/* The dropdown container */
.dropdown {
  overflow: hidden; }

/* Dropdown button */
.dropdown .dropbtn {
  position: absolute;
  right: 0.5rem;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  width: 2rem;
  height: 2rem;
  border-top: 2px solid #666;
  border-left: 2px solid #666;
  border-bottom: 2px solid #202020;
  border-right: 2px solid #202020;
  background-color: rgba(128, 128, 128, 0.25);
  font-size: 16px;
  text-align: center;
  transition: 0.2s all; }

.dropdown .dropbtn:active,
.dropdown .dropbtn:focus {
  border-right: 2px solid #666;
  border-bottom: 2px solid #666;
  border-left: 2px solid #202020;
  border-top: 2px solid #202020; }

@media (max-width: 60em) {
  .is-open.dropdown .dropbtn {
    border: 2px solid transparent;
    background-color: #fff; } }

.is-open.dropdown .dropdown-content {
  display: block; }

@media (min-width: 60em) {
  .dropdown .dropbtn {
    float: none;
    margin: 0 1rem 0 0;
    border: none;
    outline: none;
    position: static;
    background-color: transparent;
    background-color: transparent; } }

.dropdown .dropbtn::after {
  content: '';
  display: inline-block;
  transform: translateY(-50%);
  border-top: 5px solid #D72027;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent; }

.dropdown:hover .dropbtn::after {
  border-top-color: #fafafa; }

@media (max-width: 60em) {
  .is-open.dropdown .dropbtn::after {
    border-top: 0 solid transparent;
    border-bottom: 5px solid #D72027; } }

.menu-item:hover {
  background-color: #D72027; }

.menu-item:hover > a {
  text-decoration: underline; }

/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  background-color: #f9f9f9;
  min-width: 160px;
  border-color: #333333;
  border-width: 0 1px 1px 1px;
  border-style: solid;
  z-index: 1; }

.dropdown-content.is-open {
  display: block; }

.dropdown-content li {
  box-shadow: 0 11px 0 -10px #C4C4C4; }

.dropdown-content li:last-of-type {
  box-shadow: none; }

@media (min-width: 60em) {
  .dropdown-content {
    position: absolute; }
  .dropdown-content li {
    box-shadow: none; }
  /* Show the dropdown menu on hover */
  .dropdown:hover .dropdown-content {
    display: block; } }

/* Links inside the dropdown */
.dropdown-content a {
  float: none;
  color: #0a0a0a;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left; }

/* Add a grey background color to dropdown links on hover */
.dropdown-content a:hover {
  background-color: #ddd; }

.hero {
  display: flex;
  margin-left: -1rem;
  width: calc(100% + 2rem);
  margin-bottom: 2rem;
  min-height: 30vw;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat; }

.hero--small {
  margin-left: 0;
  margin-bottom: 1rem;
  max-width: 100%;
  min-height: 0;
  max-height: 200px;
  object-fit: cover; }

.hero > .row {
  margin-top: auto;
  margin-bottom: auto;
  background-color: rgba(10, 10, 10, 0.75);
  color: #fafafa;
  padding: 2rem; }

@media (min-width: 60em) {
  .hero {
    margin-left: 0;
    width: 100%; }
  .hero > .row {
    padding: 1rem; } }

.card {
  display: flex;
  margin: 0 1rem 1rem;
  padding: 1rem;
  height: auto;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #F0F0F0;
  color: #0a0a0a;
  text-decoration: none;
  text-decoration: underline transparent; }

.card:hover .card__heading,
.card:hover .card__button {
  text-decoration: underline;
  text-decoration: underline currentColor; }

.card:hover .card__icon {
  top: -0.25rem;
  transform: rotate(3deg); }

.card-icons {
  margin-top: 2.5rem;
  padding-top: 0;
  width: 100%; }

@media (min-width: 42em) {
  .card-icons {
    margin-bottom: 4rem; } }

.card__icon {
  position: relative;
  top: 0;
  margin: -2.5rem auto 0;
  padding: 1rem;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #D72027;
  color: #fafafa;
  fill: currentColor;
  transition: top 0.2s, transform 0.2s; }

.card__heading {
  margin-bottom: 1rem;
  text-align: center;
  text-decoration: underline transparent;
  transition: text-decoration 0.2s; }

.card__content {
  margin: 0;
  padding-bottom: 2rem; }

.card__button {
  margin-top: auto;
  padding: 1rem;
  background-color: #0a0a0a;
  color: #fafafa;
  text-align: center;
  text-decoration: underline transparent;
  transition: text-decoration 0.2s, background-color 0.2s; }

.card:hover .card__button {
  background-color: #D72027; }

.teacher-profile {
  flex-direction: column;
  align-items: flex-start;
  padding: 1rem;
  width: 100%;
  background-color: #F0F0F0; }

@media (min-width: 42em) {
  .teacher-profile {
    display: flex;
    max-width: 250px; } }

.teacher-profile__image + .teacher-profile__content {
  padding-top: 1rem;
  max-width: 100%; }

.teacher-profile__content {
  display: flex; }

.teacher-profile__content .column:first-of-type {
  margin-right: 1rem; }

.teacher-profile__content + * {
  padding-top: 1rem; }

.teacher-profile__image {
  width: 100%; }

.teacher-profile h3,
.teacher-profile ul {
  margin: 0; }

.news {
  background-color: #C4C4C4;
  padding: 1rem; }

.news-story {
  padding-top: 1rem;
  padding-bottom: 1rem; }

.news-story:nth-of-type(odd) {
  background-color: #F0F0F0;
  margin-left: -1rem;
  margin-right: -1rem;
  padding-left: 1rem;
  padding-right: 1rem; }

.news-header {
  align-content: flex-end;
  padding-bottom: 1rem;
  border-bottom: 1px solid #0a0a0a; }

.news-header h2 {
  margin: 0;
  padding: 0;
  align-self: flex-end; }

.news-image {
  float: left;
  margin-right: 1rem; }
